.socials {
  width: 4.8rem;
  height: 4.8rem;
  fill: $green;
  display: inherit;

  &:hover, &:focus {
    fill: $dark-green;
  }
}

.ticket, .call {
  border: 2px solid $green;
  padding: 1rem 3rem;
  border-radius: 50px;
  font-weight: bold;
}

.ticket {
  background-color: $green;
  color: $white;

  &:hover, &:focus {
    background-color: transparent;
    color: $green;
  }
}

.call {
  background-color: transparent;
  color: $green;

  &:hover, &:focus {
    background-color: $green;
    color: $white;
  }
}

//event
.event {
  p {
    margin: 0;
    position: relative;
    padding: 4.5rem 0;

    &:after, &:before {
      content: '';
      position: absolute;
      height: 1px;
      width: 140px;
      background-color: $white;
      left: 50%;
      transform: translateX(-50%);
    }

    &:after {
      bottom: 0;
    }

    &:before {
      top: 0;
    }
  }
}

//target
.target {
   .flex {
     align-items: flex-start;
   }

  .carousel {
    position: relative;
  }

  .slick-dots li button:before {
    background-color: white;
  }

  .slick-dots li.slick-active button:before {
    background-color: white;
  }
}

//map
@media all and (min-width: 768px) {
  .map {
    height: 100vh;
  }
}

@media all and (max-width: 767px) {
  .map-section {
    display: block;

    .transform-c-c {
      position: static;
      transform: translateX(0);
    }

    .map {
      height: 210px;
    }
  }
}
