.masters {
  font-size: 1.5rem;
  padding-bottom: 0;

  .sm-title {
    margin: 2rem 0 0;
  }

  .col-md-4 {
    margin: 0 auto 9rem;
  }

  span {
    color: $grey;
    font-size: 1.2rem;
    display: inline-block;
    position: relative;
    margin-bottom: 5rem;

    &:after {
      position: absolute;
      content: '';
      width: 50px;
      height: 2px;
      background-color: $green;
      bottom: -3rem;
      left: 0;
    }
  }
}

@media all and (max-width: 767px) {
  .masters {
    img {
      display: block;
      margin: 0 auto 20px;
    }

    .col-md-4 {
      margin-bottom: 5rem;
    }
  }
}